<template>
    <div class="input-component">
        <div class="input-wrapper" v-show="ready">
            <textarea
            class="chat-input"
            ref="chatinput"
            type="text"
            v-model="areaValue"
            ></textarea>
        </div>
        <div class="iconbutton" v-if="send" @click="send_text()">
            <div class="leftdummy">
                <div class="idummy">
                    <i class="far fa-paper-plane"></i>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
import f from '@/application/functions';
export default {
    name: 'InputField',

    methods: {
        send_text() {

            var event = {}

            const {chatinput} = this.$refs;
            let text = chatinput.emojioneArea.getText()
            event.textContent = text

            this.$emit('chatMessage', event, chatinput)
            this.$emit('emptyInput')
            this.send = false

            console.log('TEXT DELETED 77')
            chatinput.emojioneArea.setText('')
            chatinput.emojioneArea.setFocus()
        }
    },  
    data() {
        return {
            send: false,
            toMatch: [
                /Android/i,
                /webOS/i,
                /iPhone/i,
                /iPad/i,
                /iPod/i,
                /BlackBerry/i,
                /Windows Phone/i
            ],
            isPhone: true,
            heightUp: 40,
            areaValue: '',
            ready : false
        }
    },

    mounted() {
        this.isPhone = this.toMatch.some(toMatchItem => {
            return navigator.userAgent.match(toMatchItem);
        })

        if(this.isPhone) $('.emojionearea-button').css('display', 'none')

        $(this.$refs.chatinput).emojioneArea({
            pickerPosition : 'top',
            
            search : false,
            tones : false,
            autocomplete : false,
            attributes: {
                spellcheck : true,
            },

            filters : {
                smileys_people: {
                    icon: "yum",
                    title: "Smileys & People",
                    emoji: "grinning smiley smile grin laughing sweat_smile joy rofl relaxed blush innocent slight_smile upside_down " +
                    "wink relieved crazy_face star_struck heart_eyes kissing_heart kissing kissing_smiling_eyes kissing_closed_eyes yum " +
                    "stuck_out_tongue_winking_eye stuck_out_tongue_closed_eyes stuck_out_tongue money_mouth hugging nerd sunglasses " +
                    "cowboy smirk unamused disappointed pensive worried face_with_raised_eyebrow face_with_monocle confused slight_frown " +
                    "frowning2 persevere confounded tired_face weary triumph angry rage face_with_symbols_over_mouth " +
                    "no_mouth neutral_face expressionless hushed frowning anguished open_mouth astonished dizzy_face exploding_head flushed scream " +
                    "fearful cold_sweat cry disappointed_relieved drooling_face sob sweat sleepy sleeping rolling_eyes thinking " +
                    "shushing_face face_with_hand_over_mouth lying_face grimacing zipper_mouth face_vomiting nauseated_face sneezing_face mask thermometer_face " +
                    "head_bandage smiling_imp imp japanese_ogre japanese_goblin poop ghost skull skull_crossbones alien space_invader " +
                    "robot jack_o_lantern clown smiley_cat smile_cat joy_cat heart_eyes_cat smirk_cat kissing_cat scream_cat crying_cat_face " +
                    "pouting_cat open_hands raised_hands palms_up_together clap pray handshake thumbsup thumbsdown punch fist left_facing_fist " +
                    "right_facing_fist fingers_crossed v metal love_you_gesture ok_hand point_left point_right point_up_2 point_down point_up " +
                    "raised_hand raised_back_of_hand hand_splayed vulcan wave call_me muscle middle_finger writing_hand selfie " +
                    "nail_care ring lipstick kiss lips tongue ear nose footprints eye eyes speaking_head bust_in_silhouette " +
                    "busts_in_silhouette baby boy girl man woman blond-haired_woman blond_haired_man older_man older_woman " +
                    "man_with_chinese_cap woman_wearing_turban man_wearing_turban woman_police_officer police_officer " +
                    "woman_construction_worker construction_worker woman_guard guard woman_detective detective woman_health_worker " +
                    "man_health_worker woman_farmer man_farmer woman_cook man_cook woman_student man_student woman_singer man_singer " +
                    "woman_teacher man_teacher woman_factory_worker man_factory_worker woman_technologist man_technologist " +
                    "woman_office_worker man_office_worker woman_mechanic man_mechanic woman_scientist man_scientist woman_artist " +
                    "man_artist woman_firefighter man_firefighter woman_pilot man_pilot woman_astronaut man_astronaut woman_judge " +
                    "man_judge mrs_claus santa princess prince bride_with_veil man_in_tuxedo angel pregnant_woman breast_feeding woman_bowing " +
                    "man_bowing woman_tipping_hand man_tipping_hand woman_gesturing_no man_gesturing_no woman_gesturing_ok " +
                    "man_gesturing_ok woman_raising_hand man_raising_hand woman_facepalming man_facepalming woman_shrugging " +
                    "man_shrugging woman_pouting man_pouting woman_frowning man_frowning woman_getting_haircut man_getting_haircut " +
                    "woman_getting_face_massage man_getting_face_massage man_in_business_suit_levitating dancer man_dancing women_with_bunny_ears_partying " +
                    "men_with_bunny_ears_partying woman_walking man_walking woman_running man_running couple " +
                    "bearded_person woman_with_headscarf woman_mage man_mage woman_fairy man_fairy woman_vampire man_vampire " +
                    "mermaid merman woman_elf man_elf woman_genie man_genie woman_zombie man_zombie " +
                    "womans_clothes shirt jeans necktie dress bikini kimono high_heel sandal boot mans_shoe athletic_shoe womans_hat " +
                    "tophat mortar_board crown helmet_with_cross school_satchel pouch purse handbag briefcase eyeglasses dark_sunglasses " +
                    "closed_umbrella umbrella2 brain billed_cap scarf gloves coat socks "
                },
            },
            events: {

                emojibtn_click: () => {
                    this.send = true
                    this.$emit('FilledInput')
                },

                onLoad : (event) => {

                    this.ready = true
                    
                    const chatinput = this.$refs.chatinput;
                    const chatinput2 = this.$refs.chatinput.nextSibling;


                    const keyup = (event) => {
                        console.log('TEXT DELETED 1')
                        let text = chatinput.emojioneArea.getText();

                        if(text) {
                            console.log('TEXT DELETED 2')
                            this.send = true
                            this.$emit('FilledInput')
                        } else {
                            this.send = false
                            this.$emit('emptyInput')
                        } 

                        let url = f.getUrl(text)
                        if(url) {
                            console.log('TEXT DELETED 3')
                            if(event.keyCode !== 13 && event.keyCode !== 65 && event.keyCode !== 8 && event.keyCode !== 17) {
                                console.log('EMITTRED', event.keyCode)
                                this.$emit('setMetaUrl', url)
                            }
                        }

                        if(event.keyCode === 13 && event.shiftKey) {
                            console.log('TEXT DELETED 4')
                            this.heightUp+=10
                            event.preventDefault()
                        }else if(event.keyCode == 13 && text) {
                            console.log('TEXT DELETED 5')
                            $('.chat-input').keyup(function(e){
                                if (e.keyCode === 13 && !e.shiftKey) {
                                    e.preventDefault()
                                }
                            })

                            event.textContent = text
                            this.$emit('chatMessage', event, chatinput)
                            console.log('TEXT DELETED 6')
                            this.$refs.chatinput.emojioneArea.setText('')
                            this.$emit('emptyInput')
                            this.send = false
                        }

                    }

                    $('.chat-input').keyup(function(e){
                        if (e.keyCode == 13 && !e.shiftKey) {
                            e.preventDefault()
                        }
                    });


                    chatinput.addEventListener('keyup', keyup)
                    chatinput2.addEventListener('keyup', keyup)

                }
            }
        })
    }
}
</script>

<style lang="sass" scoped>

    /deep/ .emojionearea, .emojionearea.form-control
        background: transparent!important
        

    /deep/ .emojionearea .emojionearea-editor
        &::-webkit-scrollbar
            width: 0!important

    /deep/ .emojionearea .emojionearea-button
        top : 50%
        margin-top: -12px

    .input-component
        width: 100%
        display: flex
        justify-content: center
        align-items: center
        padding: 0 2 * $r
        padding-right: 0
        .input-wrapper
            width: 100%
        .right
            margin: 0 $r
        .iconbutton

            .leftdummy
                width: 35px

            .idummy
                width: 35px
                height: 35px
                line-height: 35px
                i
                    line-height: 35px!important
                
</style>